import './App.css';
import Movies from './components/Movies';
import Header from './components/Header';
import Pagination from './components/Pagination';
import { useState, useEffect } from 'react';



const Home_Suggestion_API = "https://api.themoviedb.org/3/discover/movie?sort_by=popularity.desc&api_key=a20e52f6f51cafc80ee323856e0e6623&page=";

function App() {


  const [movies, setMovies] = useState([]);
  //pagination
  const [pageNo, setpageNo, getValue] = useState(1)


  const PageNoUpdate = (page) => {
   
    setpageNo(page); // state setted but not immediatly reflected show for that we need to Call the updater below just to get the latest value.
    setpageNo((state) => {
      //state is the latest updated state
      async function fetchData1() {
        setMovies([]);
        const movieResponse = await (await fetch(Home_Suggestion_API + state)).json();
        await setMovies(movieResponse.results);
        console.log(movieResponse);
      }
      fetchData1();
      return state;
    });

  }

  useEffect(() => {
    async function fetchData() {
      const movieResponse = await (await fetch(Home_Suggestion_API + 1)).json();
      setMovies(movieResponse.results);
      console.log(movieResponse);
    }
    fetchData();
  }, [])

  // search aaray set
  const onUpdate = (data) => {
    setMovies(data);
  }


  return (
    <div>
      <Header onUpdate={onUpdate} />
      <div className="text-center">
        {movies.map((one_movie, key) => (
          <Movies key={one_movie.id} movie={one_movie} />
        ))}
      </div>
      <Pagination pageNo={pageNo} pageNoUpdate={PageNoUpdate} />
    </div>
  );
}

export default App;
